#wrapper-3 {
    text-align: left;
    width: 100%;
}


#box-container {
    display: table;
    clear: both;
    width: 100%;
    position: relative;

}

.box1 {

    float: left;
}

.box2 {

    float: right;
}

.box1,
.box2 {
    width: 50%;

    box-sizing: border-box;
    width: 165px;
    height: 120px;
    margin-top: 22px;

/* From https://css.glass */
background: rgba(255, 255, 255, 0.72);
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(9.8px);
-webkit-backdrop-filter: blur(9.8px);

  /*  box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.35);*/
    border-radius: 25px;
}

#sample-text {
    position: relative;
    text-align: center;
    width: 100%;
    height: 23px;
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 23px;

}

#sample-1 {
    text-align: center;
    font-weight: 800;
    font-size: 16px;
    font-style: normal;
    margin-top: 16px;

}

#sample-2 {

    position: absolute;
    font-weight: 800;
    font-size: 16px;
    font-style: normal;

    background: linear-gradient(90.28deg, #0038FF -2.91%, #FF2020 50.95%, #FFE600 101.54%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}


#sample-box1 {
    border: 4px solid #000000;
    display: flex;
    justify-content: center;
    align-items: center;
}

#sample-box2 {
    position: absolute;
    margin-top: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

}

#sample-box2::before {


    align-items: center;
    content: "";
    position: absolute;
    inset: 0;
    padding: 4px;
    border-radius: 25px 25px 0px 0px;
    background: linear-gradient(45deg, #0038FF -2.91%, #FF2020 50.95%, #FFE600 101.54%);
    -webkit-mask:
        linear-gradient(#fff 0 0) content-box,
        linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
}

#sample-box1,
#sample-box2 {
    box-sizing: border-box;

    position: relative;
    width: 100%;
    height: 50px;
    border-radius: 25px 25px 0px 0px;
}

#sample-3,
#sample-4 {
    text-align: center;
    font-weight: 800;
    font-size: 16px;
    font-style: normal;
    margin-top: 16px;
    color: #fff;
}
#sample-box3 {
    
    box-sizing: border-box;

    position: relative;
    width: 100%;
    height: 50px;
    border-radius: 25px 25px 0px 0px;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
}

#sample-box4 {
    box-sizing: border-box;

    position: relative;
    width: 100%;
    height: 50px;
    border-radius: 25px 25px 0px 0px;
    background: linear-gradient(89.69deg, #0038FF 0.51%, #FF2020 51.02%, #FFE600 98.46%, #FFE600 98.47%);
    display: flex;
    justify-content: center;
    align-items: center;
   
}