#wrapper {
    box-sizing: border-box;

    display: block;
    width: 100%;
    height: auto;
    margin-top: 20px;

/* From https://css.glass */
background: rgba(255, 255, 255, 0.72);
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(9.8px);
-webkit-backdrop-filter: blur(9.8px);
  /*  background: #FFFFFF;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.35);*/
    border-radius: 25px;
}

#wrapper-2 {
    padding-left: 31px;
    padding-right: 31px;
}

#title-box {
    position: relative;
    width: 100%;
    height: 126px;

    background: #00a030;
    border-radius: 20px 20px 0px 0px;
}

#title {
    position: absolute;
    left: 31px;
    margin-top: 30px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 37px;

    color: #FFFFFF;

}

#content {
    padding-top: 25px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;

    color: #000000;

}

#content_p {
    width: 100%;
    height: 12px;
    padding-top: 8px;
    padding-bottom: 28px;

    font-family: 'Montserrat';
    font-style: italic;
    font-weight: 500;
    font-size: 10px;
    line-height: 10px;

    color: #303030;

}


.item p {
    display: inline-block;
  margin-top: 7px;
  margin-bottom: 7px;
}

.price {
    text-align: left;
    width: 40%;

    font-style: normal;
    font-weight: 700;
    font-size: 16px;
}

.dura {
    text-align: right;
    width: 60%;

    font-weight: 500;
    font-size: 16px;
}